<template>
  <div id="app">
    <h1>COVID </h1>
    <div class="search">
      <img v-if="this.med =='Confirmados'" src="./assets/5e6d774339135.jpg" alt="">
      <img v-if="this.med =='Mortos'" src="./assets/sadness-2667464_960_720.webp" alt="">
      <img v-if="this.med =='Recuperados'" src="./assets/000_curado_brasil.jpg" alt="">
      <br>
      <br>
      <select name="med" id="med" v-model="med">
        <option value="Confirmados">Confirmados</option>
        <option value="Mortos">Mortos</option>
        <option value="Recuperados">Recuperados</option>
      </select>
      <br>
      <input type="number" v-model="num" />
      <br>
      <br>
      <button type="submit" @click="submit()">Submeter</button>
    </div>
    <div class="component" v-if="faceta.date !== ''">
      <HelloWorld :med="med" :faceta="faceta" />
    </div>
  </div>
</template>

<script>
import HelloWorld from "./components/HelloWorld.vue";

export default {
  name: "App",
  components: {
    HelloWorld,
  },
  data() {
    return {
      med: "",
      num: 5,
      faceta: {
        date: "",
        countries: [],
      },
    };
  },
  methods: {
    submit: async function() {
      const response = await fetch("https://api.covid19api.com/summary");
      const myData = await response.json();
      console.log(myData);

      this.faceta.date = myData.Date;
      this.faceta.countries = [];
      let array = [];

      if (this.med == "Confirmados") {
        array = myData.Countries.sort(
          (a, b) => Number(b.TotalConfirmed) - Number(a.TotalConfirmed)
        );
      } else if (this.med == "Mortos") {
        array = myData.Countries.sort(
          (a, b) => Number(b.TotalDeaths) - Number(a.TotalDeaths)
        );
      } else if (this.med == "Recuperados") {
        array = myData.Countries.sort(
          (a, b) => Number(b.TotalRecovered) - Number(a.TotalRecovered)
        );
      }

      for (var i = 0; i < this.num; i++) {
        this.faceta.countries.push(array[i]);
      }

      console.log(this.faceta.countries);
    }
  },
};
</script>

