<template>
  <div class="hello">
    <b>MEDIÇÃO:</b>
    {{ this.med }}
    <br />
    <b>DATA:</b>
    {{ this.faceta.date }}
    <br />
    <table>
      <thead>
        <tr class="header">
          <td>País</td>
          <td>Novas Confirmações</td>
          <td>Confirmações Totais</td>
          <td>Novas Mortes</td>
          <td>Mortes Totais</td>
          <td>Novos Recuperados</td>
          <td>Recuperados Totais</td>
        </tr>
      </thead>
      <tbody>
        <tr class="body" v-for="country in faceta.countries" v-bind:key="country.Country">
          <td>{{ country.Country }}</td>
          <td>{{ country.NewConfirmed }}</td>
          <td>{{ country.TotalConfirmed }}</td>
          <td>{{ country.NewDeaths }}</td>
          <td>{{ country.TotalDeaths }}</td>
          <td>{{ country.NewRecovered }}</td>
          <td>{{ country.TotalRecovered }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  props: {
    med: String,
    faceta: {
      date: Date,
      countries: Array,
    },
  },
  data() {
    return {};
  },
};
</script>

